/* eslint-disable */
import react from 'react'
import { View } from '@tarojs/components'
import { Toast, Sku } from '@antmjs/vantui'
import * as COMMON from './common.js'
export default function Demo() {
  const { sku, goodsList } = COMMON
  // 
  const [currentGoods, setCurrent] = react.useState()

  const itemDisable = (goodsItem) => {
    if (!goodsItem) return true
    // count
    if (goodsItem.count === 0) return true
  }

  return (
    <>
      <Toast />
      <View></View>
      <View style={{ paddingBottom: 10 }}>
        HUAWEI{currentGoods?.skuName || '--'}
      </View>
      <Sku
        sku={sku}
        goodsList={goodsList}
        onChange={(e) => setCurrent(e)}
        clickAttrDisable={(e) => Toast.show(``)}
        itemDisable={itemDisable}
        itemRender={(it) => {
          if (it.color) {
            return (
              <View className="sku-color-item">
                <View
                  className="color-item"
                  style={{ background: it.color }}
                ></View>
                <View>{it.name}</View>
              </View>
            )
          }

          return it.name
        }}
      />
    </>
  )
}
